<template>
    <div class="container">
        <h2 class="page-header"><i18n path="common.install"></i18n></h2>
        <p><i18n path="common.guide.useNpm"></i18n></p>
        <pre class="brush:bash">
            npm i v-region --save
        </pre>
        <p><i18n path="common.guide.include"></i18n></p>
        <pre class="brush:js">
            import Vue from 'vue'
            import vRegion from 'v-region';
            Vue.use(vRegion, { global config options... });
        </pre>

        <div class="panel panel-default">
            <div class="panel-body">
                <p><i18n path="common.globalConfig"></i18n></p>
                <ul>
                    <li>i18n</li>
                    <li>blank</li>
                    <li>town</li>
                    <li>search</li>
                </ul>
            </div>
        </div>

        <h2 class="page-header"><i18n path="common.deploy"></i18n></h2>
        <p>template</p>
        <script type="syntaxhighlighter" class="brush:js">
        <![CDATA[
            <template>
                <v-region @values="regionChange" class="form-control"></v-region>
            </template>
        ]]>
        </script>

        <p>script</p>
        <pre class="brush:js">
        export default {
            methods:{
                //receive selected region data
                regionChange(data){
                    console.log(data);
                }
            }
        };
        </pre>

        <h2 class="page-header"><i18n path="common.example"></i18n> <small><i18n path="region.demo.desc"></i18n></small></h2>

        <h4><i18n path="region.demo.province"></i18n></h4>
        <v-region :city="false" :area="false" class="form-control"></v-region>
        <showcode>
            <script type="syntaxhighlighter" class="brush:js;gutter: false;">
            <![CDATA[
            <v-region :city="false" :area="false" class="form-control"></v-region>
            ]]>
            </script>
        </showcode>
        <br>


        <h4><i18n path="region.demo.province"></i18n>、<i18n path="region.demo.city"></i18n></h4>
        <v-region :area="false" class="form-control"></v-region>
        <showcode>
            <script type="syntaxhighlighter" class="brush:js;gutter: false;">
            <![CDATA[
            <v-region :area="false" class="form-control"></v-region>
            ]]>
            </script>
        </showcode>
        <br>

        <h4><i18n path="region.demo.province"></i18n>、<i18n path="region.demo.city"></i18n>、<i18n path="region.demo.area"></i18n></h4>
        <v-region class="form-control"></v-region>
        <showcode>
            <script type="syntaxhighlighter" class="brush:js;gutter: false;">
            <![CDATA[
            <v-region class="form-control"></v-region>
            ]]>
            </script>
        </showcode>
        <br>

        <h4><i18n path="region.demo.province"></i18n>、<i18n path="region.demo.city"></i18n>、<i18n path="region.demo.area"></i18n>、<i18n path="region.demo.town"></i18n></h4>
        <p><i18n path="region.demo.selected"></i18n>: </p>
        <pre style="border: 0;">{{JSON.stringify(selectResult, null, 4)}}</pre>
        <v-region :town="true" @values="selectLogs" class="form-control"></v-region>
        <showcode>
            <script type="syntaxhighlighter" class="brush:js;gutter: false;">
            <![CDATA[
            <v-region :town="true" @values="selectLogs" class="form-control"></v-region>
            ]]>
            </script>
        </showcode>
        <br>

        <h4><i18n path="region.demo.init"></i18n></h4>
        <v-region :town="true" :selected="selected" class="form-control"></v-region>
        <showcode>
            <script type="syntaxhighlighter" class="brush:js;gutter: false;">
            <![CDATA[
            <v-region :town="true" :selected="selected" class="form-control"></v-region>

            //script
            data(){
                return {
                    selected: {
                        province: '350000',
                        city: '350100',
                        area: '350104',
                        town: '350104008'
                    }
                };
            }
            ]]>
            </script>
        </showcode>
        <br>

        <h4><i18n path="region.demo.disabled"></i18n></h4>
        <v-region :town="true" :disabled="true" :selected="selected"></v-region>
        <showcode>
            <script type="syntaxhighlighter" class="brush:js;gutter: false;">
            <![CDATA[
            <v-region :town="true" :disabled="true" :selected="selected"></v-region>
            ]]>
            </script>
        </showcode>
        <br>

        <h4><i18n path="region.demo.text"></i18n></h4>
        <p class="info-box">
            <v-region :town="true" :text="true" :selected="selected"></v-region>
        </p>
        <showcode>
            <script type="syntaxhighlighter" class="brush:js;gutter: false;">
            <![CDATA[
            <v-region :town="true" :text="true" :selected="selected"></v-region>

            //script
            data(){
                return {
                    selected: {
                        province: '350000',
                        city: '350100',
                        area: '350104',
                        town: '350104008'
                    }
                };
            }
            ]]>
            </script>
        </showcode>
        <br>


        <h4><i18n path="region.demo.dropdown"></i18n></h4>
        <v-region :town="true" :ui="true" @values="regionChange">
        </v-region>
        <showcode>
            <script type="syntaxhighlighter" class="brush:html;gutter: false;">
            <![CDATA[
            <!-- Use default region caller button -->
            <v-region :town="true" :ui="true">
            </v-region>
            ]]>
            </script>
        </showcode>
        <br>


        <h4><i18n path="region.demo.customCaller"></i18n> <small><a href="https://codepen.io/terry05/pen/ERNvzJ" target="_blank"><i18n path="common.codePen"></i18n></a></small></h4>
        <v-region :town="true" :ui="true" @values="regionChange">
            <button type="button" class="btn btn-default">
                {{btnText}} <i class="fa fa-fw fa-caret-down"></i>
            </button>
        </v-region>
        <showcode>
            <script type="syntaxhighlighter" class="brush:html;gutter: false;">
            <![CDATA[
            <!-- Customize region caller -->
            <v-region :town="true" :ui="true">
                <button type="button" class="btn btn-default">
                    ｛｛btnText｝｝ <i class="fa fa-fw fa-caret-down"></i>
                </button>
            </v-region>
            ]]>
            </script>
        </showcode>
        <br>

        <h4><i18n path="region.demo.column"></i18n></h4>
        <v-region :town="true" :ui="true" :column="true">
        </v-region>
        <showcode>
            <script type="syntaxhighlighter" class="brush:html;gutter: false;">
            <![CDATA[
            <!-- Use default region caller button -->
            <v-region :town="true" :ui="true" :column="true">
            </v-region>
            ]]>
            </script>
        </showcode>
        <br>

        <h2 class="page-header"><i18n path="common.options"></i18n></h2>
        <table class="table table-bordered table-striped">
            <thead>
                <tr>
                    <th><i18n path="common.tbTitle.name"></i18n></th>
                    <th><i18n path="common.tbTitle.type"></i18n></th>
                    <th><i18n path="common.tbTitle.desc"></i18n></th>
                    <th><i18n path="common.tbTitle.required"></i18n></th>
                    <th><i18n path="common.tbTitle.default"></i18n></th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>blank</td>
                    <td>Boolean</td>
                    <td><i18n path="region.option.blank"></i18n></td>
                    <td>false</td>
                    <td>true</td>
                </tr>
                <tr>
                    <td>disabled</td>
                    <td>Boolean</td>
                    <td><i18n path="region.option.disabled"></i18n></td>
                    <td>false</td>
                    <td>false</td>
                </tr>
                <tr>
                    <td>city</td>
                    <td>Boolean</td>
                    <td><i18n path="region.option.city"></i18n></td>
                    <td>false</td>
                    <td>true</td>
                </tr>
                <tr>
                    <td>area</td>
                    <td>Boolean</td>
                    <td><i18n path="region.option.area"></i18n></td>
                    <td>false</td>
                    <td>true</td>
                </tr>
                <tr>
                    <td>town</td>
                    <td>Boolean</td>
                    <td><i18n path="region.option.town"></i18n></td>
                    <td>false</td>
                    <td>false</td>
                </tr>
                <tr>
                    <td>ui</td>
                    <td>Boolean</td>
                    <td><i18n path="region.option.ui"></i18n></td>
                    <td>false</td>
                    <td>false</td>
                </tr>
                <tr>
                    <td>search</td>
                    <td>Boolean</td>
                    <td><i18n path="region.option.search"></i18n></td>
                    <td>false</td>
                    <td>true</td>
                </tr>
                <tr>
                    <td>selected</td>
                    <td>Object</td>
                    <td><i18n path="region.option.selected"></i18n><br>
                        <ul>
                            <li><code>province</code> province code</li>
                            <li><code>city</code> city code</li>
                            <li><code>area</code> area code</li>
                            <li><code>town</code> town code</li>
                        </ul>
                        <i18n path="common.example"></i18n>: <br>
                        <code>{province:'350000',city:'350100',area:'350103',town:'350103012'}</code>
                    </td>
                    <td>false</td>
                    <td></td>
                </tr>
                <tr>
                    <td>text</td>
                    <td>Boolean</td>
                    <td><i18n path="region.option.text"></i18n></td>
                    <td>false</td>
                    <td>false</td>
                </tr>
                <tr>
                    <td>column</td>
                    <td>Boolean</td>
                    <td><i18n path="region.option.column"></i18n></td>
                    <td>false</td>
                    <td>false</td>
                </tr>
            </tbody>
        </table>

        <h2 class="page-header"><i18n path="common.events"></i18n></h2>
        <table class="table table-bordered table-striped">
            <thead>
            <tr>
                <th><i18n path="common.tbTitle.param"></i18n></th>
                <th><i18n path="common.tbTitle.desc"></i18n></th>
                <th><i18n path="common.tbTitle.args"></i18n></th>
            </tr>
            </thead>
            <tbody>
            <tr>
                <td>values</td>
                <td><i18n path="region.event.values"></i18n></td>
                <td>
                    <pre class="brush:js">
                        {
                            province:{key:'',value:''},
                            city:{key:'',value:''},
                            area:{key:'',value:''},
                            town:{key:'',value:''}
                        }
                    </pre>
                </td>
            </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
    export default {
        name: "Demo",
        data(){
            return {
                selected: {
                    province: '350000',
                    city: '350100',
                    area: '350104',
                    town: '350104008'
                },
                btnText: 'please select',
                selectResult: {}
            };
        },
        methods: {
            regionChange(d){
                console.log(d);
                this.btnText = (!d.province&&!d.city&&!d.area&&!d.town)?'请选择':
                    `${this.getValue(d.province)}${this.getValue(d.city)}${this.getValue(d.area)}${this.getValue(d.town)}`.trim();
            },
            getValue(obj){
                return obj?obj.value:'';
            },
            selectLogs(data){
                if(data) this.selectResult = data;
            }
        },
        mounted(){
            SyntaxHighlighter.highlight();
        }
    }
</script>

<style lang="scss" scoped>
.info-box{
    background-color: #F5F5F5;
    border: 0;
    border-radius: 4px;
    padding: 10px;
}
</style>


